<template>
  <div id="app">
    <router-view :user="user"></router-view>
  </div>
</template>

<script>

const ERR_OK = 0;
export default {
	name: 'app',
	data(){
		return {
			user: {},
			userConfig: {}
		}
	},
	created(){
		this.getUserInfo();
	},	
	methods:{
		getUserInfo(){
			this.user = {
		        FaceUrl: "",
		        Birthday: null,
		        Sex: null,
		        Url: "",
		        Pushed: 0,
		        page: 0,
		        hobbys: [],
		        scription: "",
		        City: "",
		        EmployeeName: "华腾",
		        Post: "前端小码农",
		        photo: ["https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1507113185233&di=d498f58813eee505821214eb208142d0&imgtype=jpg&src=http%3A%2F%2Fimg4.imgtn.bdimg.com%2Fit%2Fu%3D3541219326%2C2382281130%26fm%3D214%26gp%3D0.jpg", "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1427372963,3638651623&fm=27&gp=0.jpg", "https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=187014939,2988834708&fm=15&gp=0.jpg", "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1507113371637&di=e8ce766e9f5d9ccc6f9e11beca481ea5&imgtype=0&src=http%3A%2F%2Fupload.fsonline.com.cn%2F2016%2F0715%2F1468537887784.jpg", "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1507113391477&di=bf8bfbb8a33dbd4a49c59fb6766bfcdf&imgtype=0&src=http%3A%2F%2Ftv02.tgbusdata.cn%2Fv2%2Fthumb%2Fjpg%2FREE3MSw1ODAsMTAwLDQsMywxLC0xLDAscms1MA%3D%3D%2Fu%2Fol.tgbus.com%2Fnews%2FUploadFiles_2374%2F200902%2F20090220142906312.jpg"]
		    }

			if(this.user.FaceUrl == ''){
				this.user.FaceUrl = require('./source/img/avatar.jpg');
				
			}
			if(!this.user.Birthday){
				this.user.Birthday = '-年-月-日';
			}

			if(!this.user.Url){
				this.user.Url = 'b8c56e6f1b3c35c2f4657eb66666666/personPhoto/3d0c542d1133c895ed7a392b0935c006.3';
			}
			if(this.user.page){
				if (this.user.page == 6 && this.user.Pushed == 1) {
					this.$router.push({ path: '/dictionary' });
				} else if (this.user.page == 6){
					this.$router.push({ path: '/card' });
				} else {
					this.$router.push({ path: '/setInfo',query:{page: page}});
				}
			} else {
				this.$router.push({ path: '/setInfo',query:{page: 1}});
			}
		},
		androidSetLeft: function(){
			var self = this;
			document.addEventListener('backbutton',function(e){
				e.preventDefault();
				let path = self.$route.path;
				switch(path){
	                case '/setInfo': 
	                	let page = self.$route.query.page;
	                	if (page == 1) {
	                		dd.biz.navigation.close({});
	                	} else {
	                		page--;
	                		self.$router.push({path: '/setInfo',query:{page: page}});
	                	}
	                    break;  
	                case '/card':
		                let back = self.$route.query.back;
	                	if (back) {
							self.$router.push({path: '/dictionary', query:{title: '乐新人', part: 'dictionary'}});
						} else {
							dd.biz.navigation.close({});
						}
	                    break;  
	                case '/changemsg':
	                	let hobby = self.$route.query.hobby;
	                    if (hobby) {
							self.$router.push({ path: '/changemsg',query:{hobby: false, back: true}})
						} else {
							self.$router.push({ path: '/card', query:{back: true}});
						}
	                    break;
	                case '/dictionary':
	                	dd.biz.navigation.close({});
	                	break;
	                case '/dictionary/module':
	                	self.$router.push({path: '/dictionary', query: {title: '乐新人'}});
	                	break;
	                case '/dictionary/module/content': 
	                	self.$router.push({path: '/dictionary/module', query: {title: self.$route.query.oldTitle, part: 'module'}});
	                	break;
	                case '/dictionary/examination':
	                	self.$router.push({path: '/dictionary', query:{part: 'dictionary', title: '乐新人'}});
	                	break;
	                case '/dictionary/submitPage':
	                	self.$router.push({path: '/dictionary', query: {isTest: true, part: 'dictionary', title: '乐新人'}});
	                	break;
	                case '/dictionary/submitPage/examinationSubmit':
	                	self.$router.push({path: '/dictionary/submitPage', query: {part: 'submitPage'}});
	                	break;
	            }
			});
		},
	}
}
</script>

<style>
#app {
	width: 100%;
	height: 100%;
}
</style>



