	            "Content": "印章是公司经营管理活动中行使职权，明确公司各种权利义务关系的重要凭证和工具，印章的管理应做到分散管理、相互制约，为明确使用人与管理人员的责权，特做如下公司印章使用与管理条例：    各类印章的具体管理条例：  一、印章的刻制  刻制按公司业务需要由部门提出申请，公司领导批准后，由总经办统一办理。印章刻好后由总经办办理印章登记手续，填写《印章管理登记表》备案并留好印章样图。    1.新公司成立，基本印章（公章、合同章、法人私章、财务章）的刻制，应由指定的印章管理人员负责刻制，后交由人事行政部备案，并从刻制之日起执行相关使用条例。    2.因业务发展需要申请各职能部门专用章时，由需求部门填写《印章制发申请表》经总经理核准后送交人事行政部门刻制。  二、印章的保管和使用  1.印章的保管  通过总经理授权赋予一些部门对其部门（专业）印章的保管权和使用权。由被授权人在总经办签字领取印章，指定专人保管。 2.印章的使用  任何部门在启用印章前，均需与公司（人事行政部）办理领取手续，接受相应的责权告知，签定《印章签收手续》并备案。（已经开始使用的需自通知当日补办理领取手续）"
	        }]
