	        "RuleTitle": "公司福利",
	        "isRead": 0,
	        "MenuList": [{
	            "RuleTitle": "员工守则",
	            "isRead": 0,
	            "Mark": 0,
	            "Content": "一、 遵纪守法，忠于职守，爱岗敬业。二、 维护公司声誉，保护公司利益。三、 服从领导，关心下属，团结互助。四、 爱护公物，勤俭节约，杜绝浪费。五、 不断学习，提高水平，精通业务。六、 积极进取，勇于开拓，求实创新。"
	        }, {
	            "RuleTitle": "文件管理制度",
	            "isRead": 0,
	            "Mark": 0,
	            "Content": "为减少发文数量，提高办文速度和发文质量，充分发挥文件在各项工作中的指导作用，特制订本制度。  一、文件管理内容主要包括：上级函、电、来文，同级函、电、来文，本公司上报下发的各种文件、资料。按照分工的原则，全公司各类文件由办公室归口管理。     二、收文的管理     三、公文的签收      1.凡来公司公启文件（除公司领导订启的外）均由办公室登记签收  2.对上级机要部门发来的文件，要进行信封、文件、文号、机要编号的“四对口”核定，如果其中一项不对口，应立即报告上级机要部门，并登记差错文件的文号。    3. 公文的编号保管    （1）、 办公室秘书对上级来文拆封后应及时附上“文件处理传阅单”，并分类登记编号、保管。须由公司承办或归档的公司领导亲启文件，公司领导启封后，也应交办公室办理正常手续。    （2）、本公司外出人员开会带回的文件及资料应及时分别送交办公室秘书进行登记编号保管，不得个人保存。"
	        }, {
	            "RuleTitle": "档案管理制度",
	            "isRead": 0,
	            "Mark": 1,
	            "Content": "一、严格执行党和国家的保密、安全制度，确保档案和案卷机密安全。   二、各部室应在每季度底向公司办公室移交上季度文书档案并履行清交手续。   三、各部室应明确规定档案责任人，档案责任人（档案员）对本部门档案的收集、建档、保管、借阅和利用负全责。   四、各类规章制度、办法、人事、工资资料、会议记录、会议纪要、简报、重要电话记录、接待来访记录、上级来文、公司发文、工作计划和工作总结以及添臵设备、财产的产权资料由办公室负责归档。   五、各工程项目立项、国土、规划、设计、监理、质监及技术等图纸文字技术资料、质量资料由投资发展部负责归档。   六、各类承包合同、商务合同、协议的正本原件由财务部归档，副本原件、复印件由办公室归档，其他部门备份存档并由信息中心实行电脑化管理。"
	        }, {
	            "RuleTitle": "保密制度",
	            "isRead": 0,
	            "Mark": 1,
	            "Content": "为保守公司秘密，维护公司利益，制订本制度。   一、全体员工都有保守公司秘密的义务。在对外交往和合作中，须特别注意不泄露公司秘密，更不准出卖公司秘密。   二、公司秘密是关系公司发展和利益，在一定时间内只限一定范围的员工知悉的事项。公司秘密包括下列秘密事项：   1、公司经营发展决策中的秘密事项；  2、人事决策中的秘密事项；  3、专有技术；   4、招标项目的标底、合作条件、贸易条件；  5、重要的合同、客户和合作渠道；   6、公司非向公众公开的财务情况、银行帐户帐号；  7、股东会或总经理确定应当保守的公司其他秘密事项。四、公司秘密应根据需要，限于一定范围的员工接触。接触公司秘密的员工，未经批准不准向他人泄露。非接触公司秘密的员工，不准打听公司秘密。   五、记载有公司秘密事项的工作笔记，持有人必须妥善保管。如有遗失，必须立即报告并采取补救措施。   六、对保守公司秘密或防止泄密有功的，予以表扬、奖励。  违反本规定故意或过失泄露公司秘密的，视情节及危害后果予以行政处分或经济处罚，直至予以除名。   七、档案室及相关机要重地，非工作人员不得随便进入；工作人员更不能随便带人进入。   八、办公室应定期检查各部门的保密情况。"
