				color: #222;
				line-height: 36/@r;
				text-align: center;
				font-weight: 700;
			}
		}
		.titleItem{
			width: 100%;
			height: 80/@r;
			margin-bottom: 10/@r;
			background-color: #fff;
			border: 4px solid #1B4551;
			border-radius: 4px;
			box-sizing: border-box;
			.item_text{
				float: left;
				text-indent: 1em;
				font-weight: 700;
				line-height: 72/@r;
			}
			.nextIcon{
				float: right;
				width: 25/@r;
				height: 72/@r;
				background-image: url('../source/img/icon_indexzd_into.svg');
				background-repeat: no-repeat;
				background-position: 0 25/@r;
				background-size: 13/@r 22/@r;
			}
			.isread{
				float: right;
				height: 72/@r;
				line-height: 72/@r;
				padding-right: 10/@r;
				color: #66BD5C;
				font-weight: 700;
			}
		}
	}
	.linkBar{
		position: fixed;
		bottom: 30/@r;
		right: 15/@r;
		border-radius: 50%;
		width: 50/@r;
		height: 50/@r;
		background-color: #FFFFFF;
		box-shadow: 0 2px 4px 0 rgba(0,0,0,0.30);
		z-index: 99;
		.linkBarIcon{
			border-radius: 50%;
			width: 50/@r;
			height: 50/@r;
			background-image: url(../source/img/icon_index_home.svg);
			background-size: 20/@r 20/@r;
			background-repeat: no-repeat;
			background-position: 15/@r 15/@r;
		}
		.home{
			display: block;
			position: absolute;
			top: -65/@r;
			width: 50/@r;
			height: 50/@r;
			border-radius: 50%;
			background: #FF5E3B;
			box-shadow: 0 2px 4px 0 rgba(255,94,47,0.20);
			background-image: url(../source/img/icon_index_me.svg);
			background-size: 20/@r 20/@r;
			background-repeat: no-repeat;
			background-position: 15/@r 15/@r;
			opacity: 1;
			&.show-enter-active, &.show-leave-active{
				transition: all 0.4s;
				transform: translate3d(0,0,0);
			}
			&.show-enter, &.show-leave-active{
				top: 0/@r;
				opacity: 0;
				transform: translate3d(0,0,0);
			}
		}
	}
	.hide{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0,0,0,0.6);
	}
}
</style>



